.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f0f0f0;
}

.layout {
  text-align: center;
  font-family: 'Arial', sans-serif;
}

.errorCode {
  font-size: 5rem;
  color: var(--header-color);
}

.message {
  font-size: 1.5rem;
  color: var(--tertiary-color);
}

.animation {
  margin-top: 20px;
  position: relative;
  width: 100px;
  height: 100px;
}

.ball {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: var(--header-color);
  position: absolute;
  top: 0;
  left: 25px;
  animation: bounce 1s infinite ease-in-out;
}

.shadow {
  width: 50px;
  height: 10px;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  position: absolute;
  top: 80px;
  left: 25px;
  animation: shadowBounce 1s infinite ease-in-out;
}

@keyframes bounce {
  0%, 100% {
      top: 0;
      transform: scale(1);
  }
  50% {
      top: 50px;
      transform: scale(1.2);
  }
}

@keyframes shadowBounce {
  0%, 100% {
      width: 50px;
      transform: scale(1);
  }
  50% {
      width: 60px;
      transform: scale(1.2);
  }
}
